/* Добавлять по категориям cl-элемент
/* $cl-button-bg: $Var */
*,
*:before,
*:after {
  box-sizing: border-box; }

html,
body {
  background-color: #ffffff;
  height: 100%; }

body {
  -webkit-text-size-adjust: 100%;
  margin: 0;
  padding: 0;
  vertical-align: top;
  font-size: 14px;
  line-height: 21px;
  font-family: "trebuchetms";
  color: #000000; }

img {
  max-width: 100%;
  height: auto; }

a {
  outline: none;
  text-decoration: none;
  color: #1698d9; }
  .no-touchevents a:hover,
  .touchevents a:active {
    color: #2ea8e5; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0px;
  font-size: 22px;
  font-weight: normal;
  color: #000000; }

hr {
  position: relative;
  display: block;
  background: none;
  border: none;
  border-bottom: 1px solid #b3b3b3;
  height: 1px;
  margin: 15px 0;
  font-size: 0;
  line-height: 0; }

ul {
  margin: 0px; }

ul li {
  margin-bottom: 0px;
  list-style: disc inside; }

ol {
  margin-bottom: 0px; }

.table {
  border: 1px solid #b3b3b3;
  margin: 0; }

th,
