  border: none;
  margin: 0px auto;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 24px; }

fieldset {
  margin-right: 0;
  margin-left: 0; }

.clearfix {
  position: relative;
  zoom: 1; }

.clear {
  clear: both;
  overflow: hidden;
  height: 1px;
  margin-top: -1px;
  line-height: 1px; }

form {
  position: relative;
  display: block;
  clear: both;
  zoom: 1; }

label,
.label {
  position: relative;
  display: block;
  zoom: 1;
  clear: both;
  width: 100%;
  margin: 0;
  font-weight: normal; }

input,
textarea,
select {
  position: relative;
  display: block;
  background: #fff;
  border: 1px solid #b3b3b3;
  box-shadow: none;
  outline: none;
  zoom: 1;
  width: 100%;
  vertical-align: top;
  font-family: "trebuchetms";
  color: #000000; }

input[type="file"] {
  display: block;
  position: relative;
  margin: 0; }

textarea {
  height: auto;
  resize: vertical; }

select {
  padding: 0; }

::-webkit-input-placeholder {
  opacity: 1;
  color: #000000; }

:-moz-placeholder {
  opacity: 1;
  color: #000000; }

::-moz-placeholder {
  opacity: 1;
  color: #000000; }

:-ms-input-placeholder {
